<template>
  <div id="app">
    <div class="autosuggest-container fade-in">
      <div style="position: relative;">
        <img v-show="!selected" style="position: absolute; right: 20px; height: 19px; top: 20px;" src="./assets/Vector.png" />
        <img v-show="selected" style="position: absolute; right: 17px; height: 26px; top: 17px;" src="./assets/Rolling-1s-200px.gif" />
      </div>
      <vue-autosuggest
              v-model="query"
              :suggestions="filteredOptions"
              @focus="focusMe"
              @click="clickHandler"
              @input="onInputChange"
              @selected="onSelected"
              :renderSuggestion="renderSuggestion"
              :sectionConfigs="sectionConfigs"
              :get-suggestion-value="getSuggestionValue"
              :input-props="{id:'autosuggest__input', placeholder:'Search for anything!'}"
              v-focus>
        <div><img src="./assets/Rolling-1s-200px.gif" /></div>
        <div slot-scope="{suggestion}" style="display: flex; align-items: center;width: 100%;">
          <img :style="{ display: 'flex', width: '25px', height: '25px', borderRadius: '15px', marginRight: '10px'}" :src="suggestion.item.icon" />
          <div style="{ display: 'flex', color: 'navyblue'}">{{suggestion.item.name}}</div>
        </div>
      </vue-autosuggest>
    </div>
  </div>
</template>

<script>
import { VueAutosuggest } from "vue-autosuggest";

export default {
  created: function () {
    window.addEventListener("keyup", this.keyPressed);
  },
  beforeDestroy: function () {
    window.removeEventListener('keyup', this.keyPressed)
  },
  data() {
    return {
      query: "",
      selected: "",
      sectionConfigs: {
        'default': {
          limit: 7
        }
      },
      suggestions: [
        {
          data: [
            {
              id: 1,
              type: "navigation",
              name: "Transactions",
              label: "See all transactions",
              url: "/orders",
              icon: "https://static.paddle.com/assets/images/icons/navigation/transactions.png"
            },
            {
              id: 2,
              type: "navigation",
              name: "Subscribers",
              label: "See all subscribers",
              url: "/subscriptions/customers",
              icon: "https://static.paddle.com/assets/images/icons/navigation/customers.png"
            },
            {
              id: 3,
              type: "navigation",
              name: "Overview",
              url: "/overview",
              icon: "https://static.paddle.com/assets/images/icons/navigation/dashboard.png"
            },
            {
              id: 4,
              type: "navigation",
              name: "Plans",
              url: "/subscriptions/plans",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 5,
              type: "navigation",
              name: "Products",
              url: "/products",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 6,
              type: "navigation",
              name: "Coupons",
              url: "/coupons",
              icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
            },
            {
              id: 7,
              type: "navigation",
              name: "Invoicing",
              url: "/payment-requests",
              icon: "https://static.paddle.com/assets/images/icons/navigation/invoicing.png"
            },
            {
              id: 8,
              type: "navigation",
              name: "Reports",
              url: "/reports",
              icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/reports.png"
            },
            {
              id: 9,
              type: "navigation",
              name: "Payouts",
              url: "/payouts/complete",
              icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/account.png"
            },
            {
              id: 10,
              type: "faq",
              name: "How do we calculate your MRR?\n",
              url: "https://paddle.com/support/how-we-calculate-your-mrr/"
            },
            {
              id: 11,
              type: "definition",
              name: "MRR",
              definition: "Monthly recurring revenue (MRR) is income that a business can count on receiving every single month.\n"
            }

          ]
        }
      ]
    };
  },
  computed: {
    defaultOptions() {
      return [
        {
          id: 1, 
          type: "search", 
          name: "Search Transactions for \"" + this.query + "\"",
          category: "Transactions",
          url: "/orders?start_date=2020-01-01&end_date=2020-12-31&search=" + this.query + "&action=Search",
          icon: "https://static.paddle.com/assets/images/icons/navigation/transactions.png"
        },
        {
          id: 2, 
          type: "search", 
          name: "Search Subscribers for \"" + this.query + "\"",
          category: "Subscribers",
          url: "/subscriptions/customers?search=" + this.query,
          icon: "https://static.paddle.com/assets/images/icons/navigation/customers.png"
        },
        {
          id: 3,
          type: "search",
          name: "Search Plans for \"" + this.query + "\"",
          category: "Plans",
          url: "/subscriptions/plans?search=" + this.query,
          icon: "https://static.paddle.com/assets/images/icons/navigation/catalog.png"
        },
        {
          id: 4, 
          type: "search", 
          name: "Search Sellers for \"" + this.query + "\"",
          category: "Sellers",
          url: "/admin/vendor?target=" + this.query + "&filter_balance=any&filter_vendor_active=any",
          icon: "https://static.staging.paddle-internal.com/assets/images/icons/navigation/vendor_management.png"
        }
      ]
    },
    filteredOptions() {
      let filteredData = this.suggestions[0].data.filter(option => {
          return option.name.toLowerCase().indexOf(this.query.toLowerCase()) > -1
      });
      if (this.query.length > 0) {
        for (let i = 0; i < this.defaultOptions.length; i++) {
          filteredData.push(this.defaultOptions[i]);
        }
      }

      return [
        {
          data: filteredData
        }
      ];
    }
  },
  methods: {
    clickHandler() {
      // event fired when clicking on the input
    },
    onSelected(item) {
      this.selected = item.item;
      if (window.top != window) {
        console.log("sending selected postmessage");
        window.top.postMessage(item.item.url, "https://vendors.development.paddle-internal.com/");
      }
    },
    onInputChange(text) {
      // event fired when the input changes
      console.log(text)
    },
    getSuggestionValue(suggestion) {
      return suggestion.item.name;
    },
    keyPressed(e){
      console.log(e);
      if(e.key == 'Escape') {
        console.log("sending selected postmessage");
        window.top.postMessage("closeFrame", "https://vendors.development.paddle-internal.com/");
      }
    },
    focusMe(e) {
      console.log(e) // FocusEvent
    },
    renderSuggestion(suggestion) {
      console.log(suggestion);

      if (suggestion.item.type === "definition") {
        let highlightedText = this.highlighted(suggestion.item.definition, this.query);
        return (
          <div style="display: flex;">
            <div class="category">Definition</div>
            <div class="label">{highlightedText}</div>
          </div>
        )
      }
      else if (suggestion.item.type === "faq") {
        let highlightedText = this.highlighted(suggestion.item.name, this.query);
        return (
          <div style="display: flex;">
            <div class="category">FAQ</div>
            <div class="label">{highlightedText}</div>
          </div>
        )
      }
      else if (suggestion.item.type === "search") {
        return (
          <div style="display: flex;">
            <div class="category">
              {suggestion.item.category}
              <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
            </div>
          <div class="label">Search {suggestion.item.category} for <b>{this.query}</b></div>
          </div>
        )
      }
      else if (suggestion.item.type === "navigation") {
        return (
          <div style="display: flex;">
            <div class="category">
              {suggestion.item.name}
              <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
            </div>
            <div class="label"> Go to <b>{suggestion.item.name}</b></div>
          </div>
        )
      }
      else {
        return (
          <div style="display: flex;">
                <div class="category">
                  {suggestion.item.name}
                  <span class="results-icon" style={{ background: "center / contain no-repeat url("+suggestion.item.icon+")" }} />
                </div>
            <div class="label">{suggestion.item.label}</div>
          </div>
        );
      }
    },
    highlighted(text, highlight) {
      // let re = new RegExp(q, 'gi');
      // console.log("re: "+re);
      //
      // if (q.length > 0) {
      //   console.log("text: "+text);
      //   console.log("replaced: "+text.replace(re, `<mark>$&</mark>`));
      //   var doc = new DOMParser().parseFromString(text.replace(re, `<mark>$&</mark>`), "text/html");
      //   console.log(doc.documentElement.tex);
      //   return (
      //           <span>
      //           {text.replace(re, `<mark>$&</mark>`)}
      //           </span>
      //   )
      // }
      // else {return text}

      const parts = text.split(new RegExp(`(${highlight})`, 'gi'));
      return <span>{parts.map(part => part.toLowerCase() === highlight.toLowerCase() ? <b>{part}</b> : part)}</span>;}

},
  components: {
    VueAutosuggest
  }
}
</script>

